body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.App .display-dices,
.App .player-two {
  width: 45%;
}

@media screen and (max-width: 768px) {
  .App {
    width: 100%;
    flex-direction: column;
  }
  .App .display-dices,
  .App .player-two {
    width: 100%;
  }
}

.dices {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  cursor: pointer;
}

.dices img {
  width: 30%;
  margin-top: 20px;
}

.row {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.row .col-1 {
  width: 15%;
  padding: 2px;
  background-color: dodgerblue;
  color: white;
  font-family: "Sorts Mill Goudy", serif;
  font-weight: bolder;
  border-radius: 5px;
}

.row .col-inp {
  width: 17%;
  height: 62%;
  margin-top: 4%;
  margin-left: auto;
  margin-right: auto;
  padding: 4px;
  background-color: whitesmoke;
  color: #333;
  border: 1px solid #333;
  border-radius: 5px;
  font-weight: bolder;
  cursor: pointer;
}

.row .col-inp:focus {
  outline: none;
}

.row .col-inp:disabled {
  font-weight: bolder;
  background-color: lightgrey;
}

.row .col-inp #blue {
  background-color: dodgerblue !important;
}
